



























































































import Vue from 'vue';
import Component from 'vue-class-component';
import shortid from 'shortid';
import { Database } from '@/database';

type Part = {
  category: string;
  model: string;
  count: number;
};
type PartWithId = Part & { id: string };

@Component({})

export default class App extends Vue
{
  search = '';
  dialog = false;
  message = '';

  default_part: PartWithId = {
    id: '',
    category: '',
    model: '',
    count: 1,
  };

index = -1;
  part: PartWithId = { ...this.default_part };

  headers = [
    { text: 'ID', value: 'id', width: 20 },
    { text: '種類', value: 'category' },
    { text: '型番', value: 'model' },
    { text: '個数', value: 'count' },
    { text: '操作', value: 'actions', sortable: false },
  ];
  parts: PartWithId[] = [];

  get dialogTitle()
  {
    return this.index === -1 ? '追加' : '編集';
  }

  get snackbar()
  {
    return this.message !== '';
  }

  created()
  {
    this.refresh();
  }

  refresh()
  {
    this.parts = [];
    Database.enumerate<PartWithId[]>(response => {
      this.parts = response;
    });
  }

  editPart(part: PartWithId)
  {
    this.index = this.parts.indexOf(part);
    this.part = { ...part };
    this.dialog = true;
  }

  deletePart(part: PartWithId)
  {
    Database.remove(part.id, () => {
      const index = this.parts.indexOf(part);
      this.parts.splice(index, 1);

      this.message = '削除しました';
    }, () => {
      this.message = '削除中にエラーが発生しました';
    });
  }

  save()
  {
    if (this.index === -1)
    {
      const NewPart = {
        ...this.part,
        id: shortid.generate(),
      };

      Database.insert(NewPart, () => {
        this.parts.push(NewPart);
        this.message = '追加しました';

        this.hideDialog();
      }, () => {
        this.message = '追加中にエラーが発生しました';
      });
    }
    else
    {
      Database.update(this.part, () => {
        Object.assign(this.parts[this.index], this.part);
        this.message = '更新しました';

        this.hideDialog();
      }, () => {
        this.message = '更新中にエラーが発生しました';
      });
    }
  }

  hideDialog()
  {
    this.dialog = false;
    this.index = -1;
    this.part = { ...this.default_part };
  }

  generateId()
  {
    return Math.floor(Number.MAX_SAFE_INTEGER * Math.random());
  }
}

